import { mapGetters } from 'vuex'

export default {
  name: 'list-cell',
  props: {
    item: {
      type: Object,
      defalut: null
    }
  },
  computed: {
    ...mapGetters([
      'visitedList'
    ])
  },
  components: {
  },
  methods: {
    selectItem (item) {
      this.$emit('select', item)
    },
    isVisited (id) {
      return !!this.visitedList.find(item => {
        return item.id === id
      })
    }
  }
}



