import Scroll from 'base/scroll/scroll'
import ListCell from 'base/list-cell/list-cell'
import Loading from 'base/loading/loading'
import Block from 'base/block/block'

export default {
  name: 'list',
  mixins: [],
  props: {
    list: {
      type: Array,
      default: []
    },
    pullup: {
      type: Boolean,
      default: false
    },
    pulldown: {
      type: Boolean,
      default: false
    },
    hasMore: {
      type: Boolean,
      default: false
    },
    hasRefresh: {
      type: Boolean,
      default: false
    },
    // 下拉刷新loading站位
    reveal: {
      type: Boolean,
      default: false
    },
    // 显示站位block
    isShowBlock: {
      type: Boolean,
      default: true
    },
    // 增加list为空时loading条件
    isShowLoading: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
    }
  },
  computed: {},
  components: {
    Block,
    Scroll,
    Loading,
    ListCell
  },
  watch: {},
  methods: {
    refresh () {
      this.$emit('refresh')
    },
    loadMore () {
      this.$emit('loadMore')
    },
    selectItem (item) {
      this.$emit('selectItem', item)
    }
  },
  created () {
  },
  mounted () {}
}



